/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
import React, { useState, useEffect } from "react";
import AppContexts from "../../../ontexts/AppContexts";
import {
  Toastify,
  renderToastify,
} from "../../../vendor/Toastify/ToastifyConfig";
import "./Gallery.scss";
import UnsplashApi from "../../../utils/UnsplashApi";
import Loader from "../../Loader/Loader";
import ScrollToTopButton from "../ScrollToTopButton/ToTopButton";
import ImagePopup from "../ImagePopup/ImagePopup";

function Gallery({ query }) {
  //     
  const [photos, setPhotos] = useState([]);
  //     
  const [loading, setLoading] = useState(false);
  //   
  const [currentPage, setCurrentPage] = useState(1);
  //    
  const [totalPages, setTotalPages] = useState(1);
  //   
  const [selectedPhoto, setSelectedPhoto] = useState(null);

  //   popup
  function handlePhotoClick(photo) {
    setSelectedPhoto(photo);
  }
  //  fullscreen popup
  function closeAllPopups() {
    setSelectedPhoto(null);
  }

  //      Unsplash
  const fetchPhotos = async (query, page) => {
    try {
      setLoading(true); //   
      const data = await UnsplashApi(query, page); //    API
      if (!data.results) {
        throw new Error(' API    "results"');
      }
      //   
      setPhotos((prevPhotos) => {
        const newPhotos = data.results.filter(
          (photo) => !prevPhotos.some((prevPhoto) => prevPhoto.id === photo.id)
        ); //    photo.id   
        return prevPhotos.concat(newPhotos); //      
      });
      setTotalPages(data.total_pages); //    
      setCurrentPage(page); //   
    } catch (error) {
      renderToastify(
        "error",
        ` .   .`,
        "bottom-center"
      );
      throw error; //  ,       ,   fetchPhotos()
    } finally {
      setLoading(false); //   
    }
  };

  //      
  useEffect(() => {
    if (query.trim() === "") {
      //   ,        
      return;
    }
    setPhotos([]); //   
    fetchPhotos(query, 1); //   
  }, [query]);

  //     
  const loadMorePhotos = () => {
    const nextPage = currentPage + 1; //    
    fetchPhotos(query, nextPage); //   
  };

  //    ("" )
  const handleScroll = () => {
    const { scrollTop, clientHeight, scrollHeight } = document.documentElement;
    if (
      scrollTop + clientHeight >= scrollHeight - 1 &&
      !loading &&
      currentPage < totalPages // ,    
    ) {
      loadMorePhotos();
    }
  };

  //      
  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [currentPage, loading, query, totalPages]);

  return (
    <AppContexts.Provider value={loading}>
      <Toastify />
      <ScrollToTopButton />
      <ImagePopup photo={selectedPhoto} onClose={closeAllPopups} />
      <section className="gallery">
        {photos.map((photo, index) => (
          <figure
            key={photo.id}
            onClick={() => handlePhotoClick(photo)}
            className="gallery__card"
          >
            <img
              className="gallery__img"
              src={photo.urls.raw + "&w=800"}
              alt={photo.alt_description}
            />
            {/* <div className="gallery__number">{index + 1}</div> */}
          </figure>
        ))}
        {loading && <Loader />}
      </section>
    </AppContexts.Provider>
  );
}

export default Gallery;
